    padding-left: 40px;
    column-gap: 25px;
  }
}
.accordion .accordion {
  width: 90%;
  margin: 0 auto;
}
.accordion__element:last-child {
  margin-bottom: 0;
}
.accordion__element {
  margin-bottom: 0;
  padding: 0;
}
@media (min-width: 1024px) {
  .accordion__element {
    grid-column: 1/3;
  }
}
.accordion__element.is-active > .accordion__button::after {
  opacity: 0;
}
.accordion__button {
  width: 100%;
  padding: 10px;
  text-transform: uppercase;
  cursor: pointer;
}
.accordion__wrapper {
  padding: 0;
}
@media (min-width: 1024px) {
  .accordion__wrapper {
    display: flex;
    gap: 49px;
  }
}

@media (min-width: 1024px) {
  .accordion__element--address {
    grid-column: 3/4;
  }
}
@media (max-width: 767px) {
  .accordion__element--address {
    position: relative;
  }
}

