.social {
  padding-top: 50px;

  @media (max-width: $vp-1023) {
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  @media (max-width: $vp-767) {
    grid-template-columns: 1fr;
  }
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  list-style: none;
  padding-left: 13px;
  margin-top: 34px;
  min-width: 153px;

  @media (max-width: $vp-1023) {
    grid-column: 3/4;
    margin-top: 10px;
    margin-right: 13px;
    padding: 0;
    justify-content: end;
  }

  @media (max-width: $vp-767) {
    grid-column: 1/2;
    margin-top: 4px;
    justify-content: center;
    padding-left: 18px;
    margin-bottom: 10px;
  }
}

.social__item {
  padding-right: 15px;
}

.social__item:last-child {
  padding-right: 0;
}

.social__link:hover .social__network {
  opacity: 0.8;
}

.social__link:active .social__network {
  opacity: 0.7;
}
