.header {
  position: fixed;
  top: 0;
  width: 100vw;
  //min-height: 120px;
  z-index: 5;
  max-width: 1920px;
  min-width: 1024px;
  margin: 0 auto;
  background-image: $header-color;
  padding: 0 18.2%;

  @media (max-width: $vp-1023) {
    position: static;
    padding-left: 50px;
    padding-right: 70px;
    max-width: 1023px;
    min-width: 768px;
    justify-content: space-between;
  }

  @media (max-width: $vp-767) {
    padding-left: 26px;
    padding-right: 35px;
    max-width: 767px;
    min-width: 320px;
    justify-content: space-between;
  }
}

.header__container {
  @media (max-width: $vp-1023) {
    display: flex;
    justify-content: space-between;
  }
}

.header__logo {
  display: block;
  padding-top: 30px;

  @media (max-width: $vp-1023) {
    padding-top: 20px;
  }

  @media (max-width: $vp-767) {
    padding-top: 15px;
    width: 35px;
    height: 35px;
  }
}

.header__logo:hover,
.header__logo:focus {
  opacity: 0.7;
}

.header__logo:active {
  opacity: 0.3;
}
